}

.container {
  margin-bottom:10px;
  margin-top:10rem;  
  text-align: center;
}

nav {
  position:fixed;
  top:0px;
  width: 100%;
  background-color: #e3f2fd;
  background-color: #0d265f;
}

.react-root .container {
  margin-top:75px;
  background-color: #070029;
  background-color: #0d265f;
}

section {
  overflow-y: hidden;
  z-index: 0; 
  padding: 6rem;
  padding-top:8rem; 
} 

#footer-container {
  margin-bottom:0px;
  background: #1a097c;
  background: #070029;
}

