.ischool_logo {
    width: 50%;
    height: 30%;
}

.logo_imgs {
  display: inline;
}

.logo_imgs img {
  margin: 0.5rem 0rem 0 0rem; 
}

.logo {
    width: 10%;
}

h5, h2, p {
  color: rgb(0, 0, 0);
}

nav h2 {
  color: rgb(255, 255, 255);
  font-weight: bold;
  font-size: 30pt;
}


footer p {
  font-family: Arial, Helvetica, sans-serif;
  font-family: 'Roboto', sans-serif;
  font-family: 'Lato', sans-serif;
  color: rgb(221, 144, 0);
}

.navbar-brand {
