}

#use_form {
  text-align: center;
  margin-top: 1rem;
  padding-top: 2rem;
}

.jumbotron {
  background-image:linear-gradient(to right,#005aa7,#dbdf00b3);
  color: white;
  padding-bottom: 8rem;
  margin-bottom: 4rem;
}

.jumbotron-fluid {
  margin-bottom: 4rem;
}

.lead {
  color: white;
}

